.iconsearch,
.text {
  position: relative;
  height: 22.5px;
  flex-shrink: 0;
}
.iconsearch {
  width: 26.05px;
  overflow: hidden;
}
.text {
  letter-spacing: -0.41px;
  line-height: 22px;
  display: inline-block;
  width: 314.81px;
}
.search {
  border-radius: 27px;
  background-color: #fff;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  border: 1px solid #d9d0e3;
  box-sizing: border-box;
  width: 404px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: 11px 24px;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 17px;
  text-align: left;
  font-size: 17px;
  color: #9586a8;
  font-family: "SF Pro Text";
}
